@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}
.navbar-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-top: 1.4rem;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 9rem;
}


@media (max-width:1200px) {
    .navbar-flex {
        margin: 0 4rem;
    }
}

@media (max-width: 576px) {
    .navbar-flex {
        margin: 0 1rem;
    }
}


.brass-chart-info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.account-info {
    margin-left: 9rem;
}

div.account-info >* {
    margin-bottom: 1.8rem;
}

.bank-better {
    font-size: 0.85rem;
    letter-spacing: 0.1rem;
    font-weight: bold;
    color: rgb(46, 45, 45);
}

.account-info > h1 {
    font-size: 2.2rem;
    color: rgb(53, 53, 53);
}

.account-info > p {
    color: rgb(136, 133, 133);
}

.span-color {
    color: rgb(53, 53, 53);
}

div.button-and-tel-container >* {
    margin-top: 2rem;
}

.button-and-tel-container  img {
    width: 1rem;
}

.button-and-tel-container span {
    padding: 0.2rem;
    background-image: linear-gradient(270deg,rgba(226,236,253,0),#e2ecfd);
}



@media (max-width:1200px) {
    .account-info > h1 {
        font-size: 2rem;
    }
    .brass-chart-info {
        margin-top: 4rem;
    }
    .trybrass-chart > img {
        width: 43rem;
    }
    .account-info {
        margin-left: 4rem;
    }
    
}

@media (max-width:992px) {
    .trybrass-chart > img {
        width: 37rem;
    }
}

@media (max-width:768px) {
    .trybrass-chart > img {
        width: 23rem;
    }
}

@media (max-width:576px) {
    .brass-chart-info {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .trybrass-chart > img {
        width: 33rem;
    }
    .account-info {
        margin-left: 2rem;
        text-align: center;
    }
    
}


@media (max-width:480px) {
    .brass-chart-info {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .account-info {
        margin-left: 2rem;
        text-align: center;
    }
    .trybrass-chart > img {
        width: 25rem;
    }
    
    
}


.span-advert {
    background: #e6f8f3;
    color: #10b683;
    font-size: 0.82rem;
    letter-spacing: 0.07rem;
    padding: 0.4rem 0.8rem;
    border-radius: 4px;
}

.advert-paragraph {
    font-size: 0.9rem;
    color: rgb(109, 107, 107);

}

.advert-div {
    background: #fff;
    width: 100%;
    max-width: 500px;
    margin: 2rem auto;
    padding: 1.4rem 0.2rem;
    border-radius: 40px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(202, 201, 201, 0.2), 0 6px 20px 0 rgba(179, 175, 175, 0.19);
}


@media (max-width:480px) {
    .advert-div {
        background: #e6f8f3;
        border-radius: 0;
        max-width: 800px;
        width: 100%;
    }
    .span-advert {
        display: block;
    }
    .advert-paragraph {
        color: #000;
    }
}
.form-container {
    max-width: 500px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 80vh;
    margin: 0 auto;
}

.login-form > * {
    display: block;
    width: 22rem;
    padding: 1rem;
    margin-top: 1.3rem;
    border-radius: 5px;
}

.login-form input {
    background: #f6f7f9;
    border: 1px solid rgb(236, 232, 232);
}

.login-form button {
    background: #3e7eee;
    border: 1px solid #3e7eee;
    margin: 2rem 0;
    outline: none;
    text-align: center;
    color: #fff;
    cursor: pointer;
}

.login-form a {     
     text-decoration: none;
}

.form-container > h1 {
    color: rgb(56, 55, 55);
}

.form-container p {
    font-size: 0.82rem;
    color: rgb(139, 137, 137);
    letter-spacing: 0.02rem;
}

.form-container a {
    color: inherit;
}

input:focus {
    outline-color: #3e7eee;
    outline-width: thin;
}



